.conteiner {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;
  background: url("images/background-mercado.jpg");
  justify-content: flex-start;
  align-items: flex-start;
}
.menu {
  display: flex;
  flex-direction: row-reverse;
  position: relative;
  
  right: 0%;
  top: 0%;
  color: #FFE0B1;
  
  z-index: 1;  
}
.menu ul {  
    list-style-type: none;
    margin: 0;
    padding: 0;
  
}
.menu li {
  display: inline;
}
.menu li a:hover {
  background-color: #884526a4;
}
.menu a {  
  padding: 8px;
  font-family: Risque;
  background-color: #632a04a1;
  color: #f2f2f2;
  text-decoration: none;
}
.titulo {
  position: absolute;
  width: 300px;
  height: 100px;
  left: calc(50% - 270px/2 + 0.5px);
  top: 80px;
  font-family: Risque;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 42px;
  display: flex;
  align-items: center;
  align-self: center;
  text-align: center;

  color: #fae848;

  mix-blend-mode: normal;
  backdrop-filter: blur(2px);
}
.formulario {
  position: absolute;
  width: 272px;
  height: 40px;
  left: calc(50% - 272px / 2 + 0.5px);
  top: 184px;
}
.entradaProduto {
  position: absolute;
  left: 0%;
  right: 18.38%;
  top: 0%;
  bottom: 0%;
  font-family: Risque;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  display: flex;
  align-items: center;
  text-align: center;
  height: 38px;
  border-radius: 10px;
  border-width: 0px;
  width: 240px;
  background: #FFE0B1;
  color: rgba(18, 8, 8, 0.51);
}
.botaoAdicionar {
  position: absolute;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 40px;
  align-items: center;
  text-align: center;
  background: #946c2f;
  border-width: 0px;
  color: #f2f2f2;
  position: absolute;
  width: 40px;
  height: 40px;
  right: 0px;
  top: 0px;
}
.lista {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 270px;
  min-height: 150px;
  max-height: 300px;
  left: calc(50% - 270px/2 + 0.5px);
  top: 249px;  
  align-items: center;
  background: #946C2F;
  border-radius: 19px;
  padding-bottom: 15px;  
  overflow-y: scroll;
}
.produtoEPreco {  
  position: relative;
  font-family: Risque;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: #FFE0B1;  
  left: 0px;
  top: 20px;
  height: 30px;
  display: flex;
  flex-direction: row;    
}
.produto {
  width: 160px;
  height: 30px;
  color: #FFFFFF;
  font-family: Risque;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;  
}
.preco {  
  width: 60px;
  height: 30px;  
}
.procurar {        
  position: absolute;
  top: 580px;  
  width: 150px;
  height: 40px;
  left: calc(50% - 150px/2 + 0.5px);
  border-width: 0px;
  bottom: 0%;
  font-family: Risque;
  font-style: normal;
  font-weight: normal;
  font-size: 30px;
  line-height: 30px;  
  align-items: center;
  text-align: center;
  color: #FFE0B1;
  background: #2C853A;
  border-radius: 10px;
}
.lista::-webkit-scrollbar {
  width: 5px;
}

.lista::-webkit-scrollbar-track {
  background: #632a04a1;
  border-radius: 2px;
}

.lista::-webkit-scrollbar-thumb {
background-color: rgba(230, 132, 0, 0.5);
outline: 1px solid rgba(105, 40, 0, 0.41);
}
